<template>
	<div class="home">
		<div class="box">
			<img class="img1" src="../assets/images/slogan@3x.png" alt="">
			<img class="img2" src="../assets/images/logo@2x.png" alt="">
		</div>
	</div>
</template>
<script>
export default {
	name: 'Home',
	data() {
		return {
			userInfoObj: {},
			userNameArr: ["001151515","888803996"],
		};
	},
	mounted() {
		this.$nextTick(()=>{
			this.ifLoginEquipment(); //判断登录机型函数
		})
	},
	methods: {
		//记录进入首页用户信息
		browseRecord(){
			this.$axios.post("/hopeUserInfo/insertHopeUserInfo",{
				"aamid":this.userInfoObj.aamid,
				"username":this.userInfoObj.user_name,
				"deptid":this.userInfoObj.deptid,
				"deptname":this.userInfoObj.cmpy_name,
				"odeptid":this.userInfoObj.odeptid,
				"odeptname":this.userInfoObj.tdept_name,
				"usermobile":this.userInfoObj.user_mobile,
				"useremail":this.userInfoObj.user_email,
				"officephone":this.userInfoObj.user_office_phone,
				"tdeptname":this.userInfoObj.tdept_name
			}).then(res=>{
				if (res.status === 200 && res.data.status === "000000"){
					alert("记录进入首页用户信息成功")
				}
			})
		},
		//判断登录机型函数
		ifLoginEquipment() {
			var EquipmentMsg = navigator.userAgent;
			var TogoFlag = 0;
			var Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'];
			for (var i = 0; i < Agents.length; i++) {
				if (EquipmentMsg.indexOf(Agents[i]) > 0) {
					TogoFlag = 1;
					break;
				}
			}
			if (TogoFlag == 1) {
				setTimeout(() => {
					var that = this
					window["rh"] && window["rh"].getUserInfo({}, function(result) {
						alert(result);
					 	var resultObj = JSON.parse(result)
						let userInfoObj = {}
						let odeptidArr = resultObj.ODEPT_CODE_PATH.split("^") 
						userInfoObj.aamid = resultObj.USER_LOGIN_NAME
						userInfoObj.deptid = resultObj.DEPT_CODE
						userInfoObj.odeptid = odeptidArr[odeptidArr.length-2]
						userInfoObj.user_mobile = resultObj.USER_MOBILE
						userInfoObj.user_name = resultObj.USER_NAME
						userInfoObj.user_post = resultObj.USER_POST
						userInfoObj.user_email = resultObj.USER_EMAIL
						userInfoObj.user_sex = resultObj.USER_SEX
						userInfoObj.cmpy_name = resultObj.CMPY_NAME
						userInfoObj.user_office_phone = resultObj.USER_OFFICE_PHONE
						userInfoObj.tdept_name = resultObj.TDEPT_NAME
						userInfoObj.user_birthday = resultObj.USER_BIRTHDAY
						alert(JSON.stringify(userInfoObj))
						that.userInfoObj = userInfoObj
						alert(JSON.stringify(that.userInfoObj))
						setTimeout(() => {
							that.browseRecord()
							setTimeout(() => {
								var userNameArr = ["001151515","888803996"]
								if (userNameArr.indexOf(userInfoObj.aamid) !== -1) {
									window.location.replace("http://76.7.66.5:8080/mobile123/index.html")
								} else {
									window.location.replace("http://76.7.66.5:8080/mobile123/index.html")
								}
							},2000);
						},0);
					}, function(err) {
						alert(err);
					});
				},200)
				// setTimeout(() => {
				// 	alert("未获取到用户信息")
				// 	window.location.replace("http://76.7.66.5:8080/mobile123/index.html")
				// },5000);
			} else {
				this.$router.push({ path: '路由地址', query: {} });
			}
		},
	}
};
</script>
<style lang="less" scoped>
.home{
	width: 100%;
	height: 100%;
	.box {
		width: 100%;
		height: 100%;
		background: url(../assets/images/bg@3x.png) no-repeat;
		background-size: 100% 100%;
		position: relative;
		.img1{
			width: .88rem;
			height: 2.31rem;
			position: absolute;
			top: 1.78rem;
			left: 50%;
			margin-left: -.44rem;
		}
		.img2{
			width: 1.48rem;
			height: .38rem;
			position: absolute;
			bottom: .3rem;
			left: 50%;
			margin-left: -.74rem;
		}
	}
}

</style>
